import React from "react";
import ProjectModel from "../../classes/models/ProjectModel";
import AppButton from "./AppButton";

function ProBox(props: { model: ProjectModel; index: number }) {
  var model = props.model;
  var index = props.index;
  var isEven = index % 2 == 0;

  function openProject() {
    window.open(model.projectURL, "_blank");
  }

  return (
    <>
      <div className={`pro pro__1`}>
        <div className="pro__img">
          <img src={model.image} alt="website" />
        </div>
        <div className="pro__text">
          <h3>{model.name}</h3>
          <p>{model.description}</p>
          <div className="stack">
            {model.techstack.map((stack) => (
              <p>{stack}</p>
            ))}
          </div>
          <div>
            {model.projectURL && <AppButton callback={() => openProject()} />}
          </div>
        </div>
      </div>
    </>
  );
}

export default ProBox;
